// BIM colors

// Brand
$bim-primary: #182A3D;
$bim-accent: #0696D7;
$bim-accent-hover: #38ABDF;
$bim-accent-pressed: #0584BD;

// Gray
$bim-gray-darker: #222222;
$bim-gray-dark-text: #6B787F;
$bim-gray-dark: #819099;
$bim-gray: #BCC9D1;
$bim-gray-light: #DAE1E6;
$bim-gray-lighter: #EDF0F2;
$bim-gray-lightest: #F7F9FA;
$bim-white: #FFFFFF;
$bim-shadow: rgba(32, 60, 94, .3);
$bim-shadow-light: rgba(32, 60, 94, .19);

// Orange
$bim-orange-light: #FF9933;
$bim-orange: #FF7F00;
$bim-orange-dark: #E67300;

// Danger
$bim-danger: #FF495C;
$bim-danger-hover: #FF808C;
$bim-danger-pressed: #BF3745;

// Blue
$bim-blue-light: #F2FAFD;
$bim-blue-attention: #DAF0F9;

// Green
$bim-green-checked: #20B88D;
$bim-green-light: #35C49C;
$bim-green-dark: #1B9E79;

// Status colors
$bim-status-blue: #087CD9;
$bim-status-yellow: #FFA600;
$bim-status-green: #2EB88A;
$bim-status-purple: #7A77D9;
$bim-status-red: $bim-danger;
$bim-status-gray: $bim-gray;
$bim-status-gray-dark: $bim-gray-dark;

// Notification colors
$bim-notification-info: $bim-status-blue;
$bim-notification-success: #84CC25;
$bim-notification-error: $bim-danger;
$bim-notification-warning: #FFBA0C;

// placeholder color
$bim-placeholder: $bim-gray-dark;
