@import '~@adsk/bim360-matrix-bootstrap-sass/src/variables/colors';
@import './_LoaderMixins';

.Loader {
  @include b360m-loader-size($default-loader-side-length);

  display: flex;
  justify-content: center;
  margin: 10% auto;

  &__scaling-container {
    width: $default-loader-side-length;
    height: $default-loader-side-length;
    display: flex;
    justify-content: center;
    padding: 2px;
  }

  &__animation-container {
    animation: rotate 2.4s linear infinite;
    transform-origin: center;
    display: flex;
    justify-content: center;

    @keyframes rotate {
      100% {
        transform: rotate(360deg);
      }
    }

    div {
      margin-top: -2px;
      position: absolute;
      background: $bim-accent;
      width: 4px;
      height: 4px;
      border-radius: 100%;
      transform-origin: 2px 20px;
      animation-duration: 1.9s;
      animation-name: rotate;
      animation-iteration-count: infinite;
      animation-timing-function: cubic-bezier(0.6, 0, 0.55, 1);

      // The children are divided into groups of 4.
      // Each group has it's own animation delay increment and opacity decrease functions.
      // Group#1 - the animation delay increments by 10ms, the opacity decreases by 0.08 and starts at 1.
      // Group#2 - the animation delay increments by 15ms, the opacity decreases by 0.06 and starts at 0.75.
      // Group#3 - the animation delay increments by 20ms, the opacity decreases by 0.03 and starts at 0.5.
      // Group#4 - the animation delay increments by 25ms, the opacity decreases by 0.015 and starts at 0.35.
      // Group#5 - the animation delay increments by 30ms, the opacity decreases by 0.008 and starts at 0.25.

      // More than 30ms delay in the animation between children takes separates them,
      // And so if changing this animation it's important to remember that.
      &:nth-child(1) {
        animation-delay: 0ms;
        opacity: 1;
      }

      &:nth-child(2) {
        animation-delay: 10ms;
        opacity: 0.92;
      }

      &:nth-child(3) {
        animation-delay: 20ms;
        opacity: 0.84;
      }

      &:nth-child(4) {
        animation-delay: 30ms;
        opacity: 0.76;
      }

      &:nth-child(5) {
        animation-delay: 45ms;
        opacity: 0.75;
      }

      &:nth-child(6) {
        animation-delay: 60ms;
        opacity: 0.69;
      }

      &:nth-child(7) {
        animation-delay: 75ms;
        opacity: 0.63;
      }

      &:nth-child(8) {
        animation-delay: 90ms;
        opacity: 0.57;
      }

      &:nth-child(9) {
        animation-delay: 110ms;
        opacity: 0.5;
      }

      &:nth-child(10) {
        animation-delay: 130ms;
        opacity: 0.47;
      }

      &:nth-child(11) {
        animation-delay: 150ms;
        opacity: 0.44;
      }

      &:nth-child(12) {
        animation-delay: 170ms;
        opacity: 0.41;
      }

      &:nth-child(13) {
        animation-delay: 195ms;
        opacity: 0.35;
      }

      &:nth-child(14) {
        animation-delay: 220ms;
        opacity: 0.335;
      }

      &:nth-child(15) {
        animation-delay: 245ms;
        opacity: 0.32;
      }

      &:nth-child(16) {
        animation-delay: 270ms;
        opacity: 0.305;
      }

      &:nth-child(17) {
        animation-delay: 300ms;
        opacity: 0.25;
      }

      &:nth-child(18) {
        animation-delay: 330ms;
        opacity: 0.242;
      }

      &:nth-child(19) {
        animation-delay: 360ms;
        opacity: 0.234;
      }

      &:nth-child(20) {
        animation-delay: 390ms;
        opacity: 0.226;
      }
    }
  }
}
