@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';
@import '~@adsk/bim360-matrix-react-loader/es/LoaderMixins';

.MatrixTable__container {
  // override default variables for BaseTable
  $table-prefix: MatrixTable;

  $table-font-size: $bim-font-size-small;
  $table-border-radius: 4px;
  $table-padding-left: 15px;
  $table-padding-right: 15px;
  $column-padding: 7.5px;

  $background-color: $bim-white;
  $box-shadow-color: rgba($bim-gray-dark, 0.19);
  $border: 1px solid $bim-gray-lighter;
  $header-background-color: $bim-gray-lightest;
  $header-font-weight: $bim-font-weight-bold;
  $row-hovered-background-color: rgba($bim-gray-lighter, 0.75);
  $header-cell-hovered-background-color: rgba($bim-gray-lighter, 0.75);
  $sort-indicator-hovered-color: $bim-gray-dark;
  $column-resizer-color: $bim-gray;

  $show-frozen-rows-shadow: false;
  $show-frozen-columns-shadow: true;

  @import '~react-base-table/lib/_BaseTable.scss';

  .#{$table-prefix} {
    &:not(.#{$table-prefix}--show-left-shadow) {
      .#{$table-prefix}__table-frozen-left {
        box-shadow: none;
      }
    }

    &:not(.#{$table-prefix}--show-right-shadow) {
      .#{$table-prefix}__table-frozen-right {
        box-shadow: none;
      }
    }

    &__header-row,
    &__row {
      // extend the clickable area for SelectionBox
      .SelectionBox {
        &::before {
          content: '';
          width: 30px;
          height: 30px;
          position: absolute;
          left: -8px;
          top: -8px;
        }
      }

      // extend the clickable area for ActionButton
      .ActionButton__icon {
        position: relative;

        &::before {
          content: '';
          width: 30px;
          height: 30px;
          position: absolute;
          left: -7px;
          top: -7px;
        }
      }
    }

    &__header {
      &-group-cell {
        justify-content: center;
        text-align: center;
        font-weight: $bim-font-weight-normal;
      }
    }

    &__row {
      // keep the same background for hovered row
      &:hover,
      &--hovered {
        background-color: $row-hovered-background-color !important;
      }

      &--clickable {
        &,
        &:hover {
          cursor: pointer;
        }
      }

      &--highlighted {
        background-color: $bim-blue-attention;
      }

      &--selected {
        background-color: $bim-blue-light;
      }
    }

    &__group-header {
      background-color: $bim-gray-lightest;
      color: $bim-gray-dark;
      font-weight: $bim-font-weight-bold;

      &.#{$table-prefix}__row {
        &:hover,
        &--hovered {
          background-color: $bim-gray-lightest !important;
        }
      }

      .ExpandIcon {
        color: $bim-gray-dark;
      }
    }

    &__empty-content {
      height: 100%;
    }

    &__loading-layer {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba($bim-white, 0.3);
      margin: 0;
      width: 100%;
      height: 100%;
    }

    &__loading-more {
      &-layer {
        pointer-events: none;
        background: $bim-shadow;
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translateX(-50%);
        padding: 5px 15px;
        border-radius: 10px;
        display: flex;
        align-items: center;
      }

      &-loader {
        @include b360m-loader-size(20px);
        margin: 0;
      }

      &-text {
        color: $bim-white;
        margin-right: 5px;
      }
    }

    &__settings-icon--active {
      &,
      &:focus {
        color: $bim-accent-hover;
      }
    }
  }
}
