@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.AnchorButton {
  font-weight: inherit;
  vertical-align: inherit;
  white-space: inherit;
  text-align: inherit;
  font-style: inherit;
  font-size: inherit;
  // sass-lint:disable no-misspelled-properties
  caret-color: inherit;
  // sass-lint:enable no-misspelled-properties
}

.AnchorButton--hover {
  color: $bim-accent-pressed !important;
}

.AnchorButton--disabled {
  color: $bim-gray !important;
}
