@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.SettingsPanel {
  width: 240px;

  &__search {
    border-top: none;
    border-left: none;
    border-right: none;

    .SearchBox {
      &__search-button {
        padding-left: 14px;
        border: none;
      }

      &__input {
        padding: 8px;
      }
    }
  }

  &__menu {
    background-color: $bim-gray-lightest;
    max-height: 300px;
    overflow-y: auto;
  }

  &__group-header {
    @include text-overflow();

    background-color: $bim-gray-lighter;
    font-weight: $bim-font-weight-semi-bold;
    padding: 4px 10px 4px 14px;
  }

  &__group-description {
    @include text-overflow();

    font-size: $bim-font-size-x-small;
    color: $bim-gray-dark;
  }

  &__item {
    position: relative;
    background-color: $bim-white;

    &:hover {
      .SettingsPanel__item-handle {
        display: block;
        color: $bim-gray-dark;

        &:hover {
          color: $bim-accent;
        }
      }
    }

    &--dragging {
      pointer-events: initial !important;
      box-shadow: 0 2px 4px 0 $bim-shadow-light;
      z-index: 99999;

      .SettingsPanel__item-handle {
        cursor: grabbing;
      }
    }

    &-handle {
      display: none;
      cursor: grab;
      color: $bim-gray-light;
      width: 20px;
      height: 20px;
      position: absolute;
      left: -3px;
      top: 50%;
      transform: translateY(-50%);

      &:hover {
        color: $bim-accent;
      }
    }
  }

  &__no-results {
    background-color: $bim-white;
    color: $bim-gray;
    padding: 8px 14px;
  }

  &__footer {
    border-top: 1px solid $bim-gray-light;
  }

  &__reset {
    background-color: $bim-white;

    &:not(:first-child) {
      border-top: 1px solid $bim-gray-light;
    }

    &-button {
      background-color: $bim-white;
      height: 30px;
      padding: 0 14px;
      text-align: left;
      outline: none;
      font-weight: $bim-font-weight-bold;
      color: $bim-accent;
      cursor: pointer;
      border: none;

      &[disabled] {
        color: $bim-gray;
        cursor: default;
      }
    }
  }
}
