@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.SearchBox {
  @include composite-input();
  position: relative;

  &:not(&--disable-focus-indication) {
    &:focus-within {
      @include focused-input();
    }
  }

  &__input {
    @include searchInputNormalizer;
    padding-left: 5px;
    padding-right: 0;

    &:hover,
    &:active,
    &:focus {
      outline: none;
      border: none;

      // sass-lint:disable no-vendor-prefixes
      &::-ms-clear {
        display: none;
      }
      // sass-lint:enable no-vendor-prefixes
    }

    &:disabled {
      background-color: $bim-white;
    }
  }
}

.SearchBox__search-button {
  display: flex;
  padding-right: 0;
  flex: none;
}

.SearchBox__close-button {
  display: flex;
  padding-right: 10px;
  flex: none;

  &--hidden {
    visibility: hidden;
  }
}
