@import './borders';

$input-color-focus: $bim-gray-darker;
$input-color-disabled: $bim-gray;
$input-border-disabled: $bim-gray;

@mixin text-input() {
  color: $input-color;
  border-radius: $border-radius-base;
  border: 1px solid $input-border;
  height: 35px;
  width: 222px;
  padding: 8px 10px;

  &.active,
  &:active,
  &--focused,
  &.focus,
  &:focus {
    &,
    &:hover {
      outline: none;
      color: $input-color-focus;
      border-color: $input-border-focus;
    }
  }

  &.hover,
  &:hover {
    border-color: $bim-gray;
  }

  &.disabled,
  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus {
      color: $input-color-disabled;
      border-color: $input-border-disabled;
      background: $bim-white;
    }
  }
}

// This input container is used for complex input boxes, such as input boxes with icons (like SearchBox or DropDownInput)
@mixin composite-input() {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  padding-right: 0;
  color: $bim-gray-dark;
  border-radius: $border-radius-base;
  border: 1px solid $bim-gray-light;
  height: 35px;
  background-color: $bim-white;

  &__input {
    border: none;
    display: block;
    width: 100%;
    height: 33px;
    padding: 8px 0 8px 13px;
    font-size: $bim-font-size-small;
    color: $bim-gray-darker;
  }
}

@mixin focused-input() {
  box-shadow: 0 2px 4px 0 rgba($bim-gray-dark, .2);
}
