@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

@mixin resetButtonSize {
  height: auto;
  padding: 0;
  border: 0;
}

.Button {
  @include btn;

  &--default {
    @include btn-default;
  }

  &--primary {
    @include btn-primary;
  }

  &--danger {
    @include btn-danger;
  }

  &--cancel {
    @include btn-cancel;
  }

  &--link {
    @include btn-link;
  }

  &--link-secondary {
    @include btn-link-secondary;
  }

  &--icon {
    @include btn-icon;
  }

  &--icon-minimal {
    @include btn-icon;
    @include resetButtonSize;
    display: flex;
  }

  &--navigation {
    @include btn-navigation;
  }

  &--inline-link {
    @include btn-link;
    @include resetButtonSize;
    font-weight: $bim-font-weight-bold;
  }
}
