@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.ContextMenu {
  position: fixed;
  z-index: 999;
  visibility: hidden;
  list-style: none;
  margin: 0;
  padding: 0;
  background: $bim-white;
  border-radius: 5px;
  border: 1px solid $bim-gray-light;
  box-shadow: 0 2px 4px 0 $bim-shadow;
  margin-top: 6px;

  &__arrow {
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    top: 0;
    left: 50%;
    border: 4px solid;
    border-color: transparent transparent $bim-white $bim-white;
    box-shadow: -1px 1px 0 0 $bim-gray-light;
    transform: translateX(-50%) translateY(-50%) rotate(135deg);
  }

  &--top {
    margin-top: -6px;

    .ContextMenu__arrow {
      top: auto;
      bottom: 0;
      transform: translateX(-50%) translateY(50%) rotate(-45deg);
    }
  }

  .MenuItemIcon {
    padding: 5px 20px 5px 10px;

    &__label {
      margin-left: 10px;
    }
  }
}
