@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.SettingsPanelItem {
  background-color: $bim-white;
  font-size: $bim-font-size-small;
  color: $bim-gray-darker;
  cursor: pointer;
  display: flex;
  flex: 1;
  align-items: center;
  padding: 0 10px 0 14px;
  height: 30px;

  &:hover {
    background-color: $bim-gray-lightest;

    .SettingsPanelItem__icon {
      display: flex;
    }
  }

  &--disabled {
    cursor: default;

    &:hover {
      background-color: $bim-white;
    }
  }

  &--frozen {
    .SettingsPanelItem__icon {
      display: flex;
    }
  }

  &__label {
    @include text-overflow();

    margin: 0 4px 0 8px;
    flex: 1;
  }

  &__icon {
    display: none;

    .SvgIcon {
      width: 20px;
      height: 20px;
    }

    &--active {
      .SvgIcon {
        color: $bim-accent;
      }
    }
  }
}
