@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.EllipsisText {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  &__tooltip {
    font-size: $bim-font-size-x-small;
    white-space: pre-wrap;
    word-wrap: break-word;
    margin: 6px 10px;

    padding: 2px 7px;
    border-radius: 3px;
    background-color: rgba($bim-primary, 0.9);
    color: $bim-white;
    box-shadow: none;
    z-index: 99999;

    &-arrow {
      position: absolute;
      width: 12px;
      height: 6px;
      bottom: -6px;

      &::after {
        content: '';
        display: block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 6px 6px 0;
        border-color: rgba($bim-primary, 0.9) transparent transparent;
      }
    }

    &--bottom {
      .EllipsisText__tooltip-arrow {
        top: -6px;
        transform: rotate(180deg);
      }
    }

    &--light {
      border: 1px solid $bim-gray-light;
      background-color: $bim-white;
      color: $bim-gray-darker;
      font-size: $bim-font-size-small;
      border-radius: 4px;
      box-shadow: 0 -2px 4px 0 $bim-shadow;
      padding: 5px 10px;

      .EllipsisText__tooltip-arrow {
        &::after {
          border-width: 5px 5px 0;
          border-color: $bim-white transparent transparent;
          margin-top: -6px;
          margin-left: 1px;
        }

        &::before {
          content: '';
          display: block;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 6px 6px 0;
          border-color: $bim-gray-light transparent transparent;
        }
      }
    }

    &--warning {
      background-color: $bim-orange;
      color: $bim-white;
      font-size: $bim-font-size-small;
      border-radius: 4px;
      box-shadow: 0 -2px 4px 0 $bim-shadow;
      padding: 5px 10px;

      .EllipsisText__tooltip-arrow {
        &::after {
          border-color: $bim-orange transparent transparent;
        }
      }
    }
  }
}

// disable the builtin tooltip for truncated text on Safari
// css hack to only apply on webkit
// sass-lint:disable force-pseudo-nesting
@media screen and (-webkit-min-device-pixel-ratio: 0) {
  @supports (not (-ms-ime-align: auto)) {
    .EllipsisText::before {
      content: '';
      display: block;
      width: 0;
      height: 0;
    }
  }
}
// sass-lint:enable force-pseudo-nesting
