@import '../variables/all-variables';
@import './buttons-bs-original-partials';
@import '../mixins/buttons';

$btn-cancel-color: $bim-gray-dark;
$btn-cancel-bg: $bim-gray-lighter;
$btn-cancel-border: $bim-gray-light;

// Override basic button class
@mixin btn {
  @include bs-btn;
  border-radius: 2px;
  height: 35px;
  padding: 8px 13px;

  &.disabled,
  &[disabled] {
    &,
    &:hover,
    &:focus {
      @include button-variant($bim-gray, $bim-gray-lightest, $bim-gray-light);
      cursor: default;
    }
  }
}

%btn-brand {
  min-width: 120px;
}

// Override button brands styles
@mixin btn-default {
  @include bs-btn-default;
  @extend %btn-brand;
  @include set-button-hover-active-states($bim-white, $bim-accent, $bim-accent, $bim-white, $bim-accent-pressed, $bim-accent-pressed, $btn-default-color, $btn-default-bg, $btn-default-border);
}

@mixin btn-primary {
  @include bs-btn-primary;
  @extend %btn-brand;
  @include set-button-hover-active-states($bim-white, $bim-accent-hover, $bim-accent-hover, $bim-white, $bim-accent-pressed, $bim-accent-pressed, $btn-primary-color, $btn-primary-bg, $btn-primary-border);
}

@mixin btn-danger {
  @include bs-btn-danger;
  @extend %btn-brand;
  @include set-button-hover-active-states($bim-white, $bim-danger-hover, $bim-danger-hover, $bim-white, $bim-danger-pressed, $bim-danger-pressed, $btn-danger-color, $btn-danger-bg, $btn-danger-border);
}

@mixin btn-cancel {
  @extend %btn-brand;
  @include button-variant($btn-cancel-color, $btn-cancel-bg, $btn-cancel-border);
  @include set-button-hover-active-states($bim-gray-dark, $bim-gray-lightest, $bim-gray-light, $bim-gray-dark, $bim-gray-light, $bim-gray-light, $btn-cancel-color, $btn-cancel-bg, $btn-cancel-border);
}

@mixin btn-link {
  @include bs-btn-link;
  @include btn-transparent-variant($bim-accent, $bim-accent-hover, $bim-accent-pressed, $bim-gray);
}

@mixin btn-link-secondary {
  @include btn-transparent-variant($bim-gray-dark, $bim-accent-hover, $bim-accent-pressed, $bim-gray);
}

@mixin btn-icon {
  @include btn-transparent-variant();

  height: auto;
  padding: 1px 7px 2px;
  border-radius: 0;
}

@mixin btn-navigation {
  @include btn-transparent-variant($bim-gray-darker, $bim-accent-hover, $bim-accent-pressed, $bim-gray);
}

