@import '~@adsk/bim360-matrix-bootstrap-sass/src/variables/colors';

.MenuItemIcon {
  color: $bim-primary;
  display: flex;
  align-items: center;
  line-height: 1.3;
  padding: 7px 23px 7px 12px;
  text-decoration: none;
  white-space: nowrap;
  width: 100%;

  &:hover {
    &:not(.MenuItemIcon--disabled) {
      background-color: $bim-gray-lighter;
      color: $bim-gray-darker;
    }
  }

  &--disabled {
    cursor: default;
  }
}

.MenuItemIcon__label {
  margin-left: 12px;
}
