@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.Tooltip {
  z-index: 9999;

  &--dark {
    color: $bim-white;
    font-size: $bim-font-size-x-small;
    border-radius: 3px;
    border: none;
    background-color: rgba($bim-primary, 0.9);
    padding: 2px 7px;

    &[data-placement] {
      box-shadow: none;
    }

    .Tooltip__arrow {
      &::after {
        display: none;
      }

      &::before {
        border-color: transparent transparent rgba($bim-primary, 0.9);
      }
    }
  }

  &--warning {
    border: none;
    background-color: $bim-orange;
    color: $bim-white;

    .Tooltip__arrow {
      &::after {
        border-color: transparent transparent $bim-orange;
      }

      &::before {
        border-color: transparent transparent rgba($bim-orange, 0.9);
      }
    }
  }
}
