@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.Popover {
  font-size: $bim-font-size-small;
  border-radius: 4px;
  border: 1px solid $bim-gray-light;
  background-color: $bim-white;
  padding: 5px 10px;
  z-index: 999;
  min-width: 30px;

  &__arrow {
    position: absolute;
    width: 12px;
    height: 6px;

    &::after {
      content: '';
      display: block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 5px 5px;
      border-color: transparent transparent $bim-white;
      margin-top: -5px;
      margin-left: 1px;
    }

    &::before {
      content: '';
      display: block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 6px 6px;
      border-color: transparent transparent $bim-gray-light;
    }
  }

  &[data-placement*='bottom'] {
    margin-top: 6px;
    box-shadow: 0 2px 4px 0 $bim-shadow;

    .Popover__arrow {
      margin: 0 10px;
      top: -6px;
      transform: rotate(0deg);
    }
  }

  &[data-placement*='top'] {
    margin-bottom: 6px;
    box-shadow: 0 -2px 4px 0 $bim-shadow;

    .Popover__arrow {
      margin: 0 10px;
      bottom: -6px;
      transform: rotate(180deg);
    }
  }

  &[data-placement*='right'] {
    margin-left: 6px;
    box-shadow: 2px 0px 4px 0 $bim-shadow;

    .Popover__arrow {
      margin: 6px 0;
      left: -9px;
      transform: rotate(-90deg);
    }
  }

  &[data-placement*='left'] {
    margin-right: 6px;
    box-shadow: -2px 0px 4px 0 $bim-shadow;

    .Popover__arrow {
      margin: 6px 0;
      right: -9px;
      transform: rotate(90deg);
    }
  }

  &--no-arrow {
    margin: 0;
  }

  &--no-padding {
    padding: 0;
  }
}
