@import '~@adsk/bim360-matrix-bootstrap-sass/src/_allPartials.scss';

.ActionButton {
  display: inline-block;
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1;

  &--open {
    color: $bim-accent-hover;

    &:focus {
      color: $bim-accent-hover; //to override the button focus state
    }
  }

  &__menu {
    list-style: none;
    margin: 0;
    padding: 0;

    .MenuItemIcon {
      &__label {
        margin-left: 10px;
      }
    }
  }
}
