$default-loader-side-length: 40px;

@mixin b360m-loader-size($loader-side-length) {
  $scale-factor: $loader-side-length / $default-loader-side-length;

  height: $loader-side-length;
  width: $loader-side-length;

  .Loader__scaling-container {
    transform: scale($scale-factor);
    transform-origin: top center;
  }
}
